@import url('https://fonts.googleapis.com/css?family=Roboto');

html, body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  letter-spacing: 1px;
  font-size: 16px;
  height: 100%;
  width: 100%;
  color: #444;
}

#root {
  height: 100%;
  width: 100%;
}

a, a:hover, a:focus, a:target {
  text-decoration: none;
}

.container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  height: calc(100% - 80px);
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.success-text {
  color: #4BB543;
  padding: 10px 25px;
}

.retry-button {
  border: none;
  border-radius: 0;
  background-color: #4BB543;
  color: white;
  font-size: 20px;
  padding: 10px 20px;
}

.retry-button--fail {
  background-color: #FA2A00;
  color: white;
}

.error-text {
  color: #FA2A00;
}

.success-text {
  color: #4BB543;
}

.status-icon {
  width: 150px;
  height: 150px;
}

.ring-2 {
    position: relative;
    width: 45px;
    height: 45px;
    margin: 0 auto;
    border: 4px solid #4b9cdb;
    border-radius: 100%;
}

.ball-holder {
  position: absolute;
  width: 12px;
  height: 45px;
  left: 17px;
  top: 0px;
}

.ball {
  position: absolute;
  top: -11px;
  left: 0;
  width: 16px;
  height: 16px;
  border-radius: 100%;
  background: #4282B3;
}

.load .ball-holder {-webkit-animation: loading 1.3s linear infinite;animation: loading 1.3s linear infinite;}

@-webkit-keyframes loading {
  0 {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
  100% {-webkit-transform: rotate(360deg);transform: rotate(360deg);}
}

@keyframes loading {
  0 {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
  100% {-webkit-transform: rotate(360deg);transform: rotate(360deg);}
}

#email-input {
  border: 1px solid gray;
  padding: 20px;
}

#email-submit {
  background: #4282B3;
  color: white;
  padding: 20px;
}



